import Header from "../../components/Header.vue";
import Footer from "../../components/Footer.vue";
import Condividi from "../../components/Condividi.vue";
import DataHeader from "../../components/DataHeader.vue";
import { StringRenderer } from "../../../commons/js/string_renderer.js";
import "../../js/consiglioinforma.js";
import { Paginazione } from "../../../commons/js/render_paginazione.js";
import { ConsiglioInforma } from "../../js/consiglioinforma.js";
import { Utils } from "../../../commons/js/utils";

export default {
    name: "app",
    components: {
        Footer,
        Header,
        Condividi,
        DataHeader
    },
    data: function () {
        $(document).ready(renderNotizie);
        return {};
    }
};


function renderNotizie() {

    var urlParams = new URLSearchParams(window.location.search);
    const tipo = urlParams.get('rivista');
    const anno = urlParams.get('anno');

    var size = 50;
    var url_to_call = apibase.baseurl + '10530?size=' + size;
    const filtro = {
        tipologia: ConsiglioInforma.Riviste[tipo].enum,
        anno_pubblicazione: anno
    };

    $.ajax({
        url: url_to_call,
        method: 'POST',
        data: JSON.stringify(filtro),
        contentType: 'application/json',
        dataType: 'json',
        success: render,
    });
    renderBreadcrumb();


    function renderBreadcrumb() {
        let bcSezione = '<li class="breadcrumb-item">\
                            <a href="anni-rivista.html?rivista=' + tipo + '">' + ConsiglioInforma.Riviste[tipo].label + '</a>\
                            <span class="separator">/</span>\
                        </li>\
                        <li class="breadcrumb-item">' + anno + '</li>';
        $('#bread').append(bcSezione);
    }

    function render(result) {
        if (result != null && result.content != undefined) {
            var newsHTML = "";
            let elencoNews = result.content;
            elencoNews.forEach(function (single) {
                var paginaDettaglio = 'rivista.html?id=' + single.id;
                var footerElemento = "";
                var stringedDate = "";
                if (single.properties.data_pubblicazione != undefined) {
                    stringedDate = StringRenderer.renderDataTestuale(single.properties.data_pubblicazione);
                } else if (single.lastModifiedDate) {
                    stringedDate = StringRenderer.renderDataTestuale(single.lastModifiedDate);
                }
                if (urlParams.get('ref_type_id') == 10061) {
                    stringedDate = "";
                }

                var descrizione = "";
                var sottotitolo = "";

                if (single.properties.descrizione != undefined && urlParams.get('ref_type_id') != 10030) {
                    descrizione = single.properties.descrizione;
                }
                if (single.properties.sottotitolo != undefined) {
                    sottotitolo = single.properties.sottotitolo;
                }
                if (single.properties.intestazione != undefined) {
                    descrizione = single.properties.intestazione;
                }
                if (descrizione != "") {
                    descrizione = '<p>' + descrizione + '</p>';
                }
                var immagine, address, imgSrc, alt = '';
                if (single.components.immagine.components.length > 0) {
                    immagine = single.components.immagine.components[0];
                    address = immagine.components.file.components[0].properties.thumbaddress;
                    imgSrc = Utils.creaUrlImmagine(address);
                    alt = immagine.title;
                }
                newsHTML += '<div class="media mb-4 col-12 col-md-6">\
                                <img class="shadow-sm mr-1" src="' + imgSrc + '" alt="' + alt + '">\
                                <div class="media-body">\
                                    <div class="col-12 dark-obj-name"> ' + stringedDate + '</div>\
                                    <div class="col-12">\
                                        <h5 class="dark-obj-name-bold">\
                                            <a class="dark-obj-name" href="' + paginaDettaglio + '">' + single.title + " " + sottotitolo + '</a>\
                                        </h5>\
                                    </div>\
                                    <div class="col-12"> ' + descrizione + footerElemento + '</div>\
                                </div>\
                            </div>';

            });
            $("#fascia_elenco").append('<div class="row">' + newsHTML + '</div>');
        }

        $("#fascia_elenco").append(Paginazione.renderizzaComponente(result));

        if (result.totalPages == 0) {
            $("#fascia_elenco").html("Nessun risultato trovato");
        }

    }


}



